"use client"

import Link from "next/link";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Menu, Globe, User, LogOut } from "lucide-react";
import { useAuth } from "@/contexts/AuthContext";
import { useState } from "react";
import UserDropdown from "../header/UserDropdown";
import { ThemeToggleButton } from "@/components/common/ThemeToggleButton";

export default function Navbar() {
  const { user, logout, isAuthenticated, loading } = useAuth();
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const handleLogout = () => {
    logout();
    setIsMenuOpen(false);
  };

  return (
    <header className="bg-white dark:bg-gray-900 border-b border-gray-100 dark:border-gray-700 p-3 sticky top-0 z-50 transition-shadow duration-300 ease-in-out shadow-sm hover:shadow-md">
      <div className="container mx-auto px-4">
        <div className="flex items-center justify-between h-12">
          <Link href="/" className="flex items-center space-x-2">
            <Image
              src="/logo.png"
              alt="ONE HEALTHLINE CONNECT"
              width={80}
              height={80}
              priority
            />
          </Link>

          {/* Desktop Nav */}
          <nav className="hidden md:flex items-center space-x-8">
            {["/", "/services", "/departments", "/about", "/contact"].map((href, idx) => {
              const label =
                href === "/"
                  ? "Home"
                  : href.replace("/", "").replace(/^\w/, (c) => c.toUpperCase());
              return (
                <Link
                  key={idx}
                  href={href}
                  className="text-gray-900 dark:text-gray-200 hover:text-green-600 dark:hover:text-green-400 font-medium transition-colors duration-300 ease-in-out"
                >
                  {label}
                </Link>
              );
            })}
          </nav>

          <div className="flex items-center space-x-4">
            <Button
              variant="outline"
              size="sm"
              className="hidden md:flex items-center border-gray-200 dark:border-gray-700 transition-colors duration-300 ease-in-out hover:bg-green-50 dark:hover:bg-green-900"
            >
              <Globe className="mr-2 h-4 w-4 text-blue-600" />
              EN | KIN
            </Button>

            <ThemeToggleButton />

            <UserDropdown />

            {!loading && (
              <>
                {isAuthenticated ? (
                  <div className="hidden md:flex items-center space-x-2 px-3 py-1 bg-green-50 dark:bg-green-900 rounded-md transition-colors duration-300 ease-in-out hover:bg-green-100 dark:hover:bg-green-700">
                    <User className="h-4 w-4 text-green-600" />
                    <span className="text-sm font-medium text-green-700 dark:text-green-300">{user?.name}</span>
                  </div>
                ) : null}

                {isAuthenticated ? (
                  <Button
                    onClick={handleLogout}
                    variant="outline"
                    size="sm"
                    className="text-red-600 border-red-200 dark:border-red-700 hover:bg-red-50 dark:hover:bg-red-900 transition-colors duration-300 ease-in-out"
                  >
                    <LogOut className="mr-2 h-4 w-4" />
                    Logout
                  </Button>
                ) : (
                  <Link href="/auth">
                    <Button className="bg-green-600 hover:bg-green-700 hidden md:flex transition-colors duration-300 ease-in-out">
                      Sign In
                    </Button>
                  </Link>
                )}
              </>
            )}

            {/* Mobile menu toggle */}
            <Button
              variant="ghost"
              size="icon"
              className="md:hidden"
              aria-label="Toggle menu"
              aria-expanded={isMenuOpen}
              onClick={() => setIsMenuOpen((prev) => !prev)}
            >
              <Menu className="h-6 w-6" />
            </Button>
          </div>
        </div>

        {/* Mobile menu (conditionally rendered) */}
        {isMenuOpen && (
          <nav className="md:hidden mt-4 space-y-2 border-t border-gray-200 dark:border-gray-700 pt-4">
            {["Home", "Services", "Departments", "About", "Contact"].map((label, idx) => {
              const href = label === "Home" ? "/" : `/${label.toLowerCase()}`;
              return (
                <Link
                  key={idx}
                  href={href}
                  className="block px-3 py-2 text-gray-900 dark:text-gray-200 hover:bg-green-50 dark:hover:bg-green-900 rounded-md transition-colors duration-300 ease-in-out"
                  onClick={() => setIsMenuOpen(false)}
                >
                  {label}
                </Link>
              );
            })}

            {!loading && !isAuthenticated && (
              <Link href="/auth">
                <Button
                  className="w-full mt-2 bg-green-600 hover:bg-green-700 transition-colors duration-300 ease-in-out"
                  onClick={() => setIsMenuOpen(false)}
                >
                  Sign In
                </Button>
              </Link>
            )}

            {isAuthenticated && (
              <Button
                onClick={() => {
                  handleLogout();
                  setIsMenuOpen(false);
                }}
                variant="outline"
                className="w-full mt-2 text-red-600 border-red-200 dark:border-red-700 hover:bg-red-50 dark:hover:bg-red-900 transition-colors duration-300 ease-in-out"
              >
                <LogOut className="mr-2 h-4 w-4 inline" />
                Logout
              </Button>
            )}
          </nav>
        )}
      </div>
    </header>
  );
}
